import React, { Component } from 'react';
import marked from 'marked';

import Input from './components/input/input';
import Output from './components/output/output';


marked.setOptions({
    pedantic: false,
    gfm: true,
    tables: true,
    breaks: true,
    sanitize: false,
    smartLists: true,
    smartypants: false,
    xhtml: false
  });
  

  const content = `                        # Why React?
  ## I certainly didn\'t need if for my 2-component 1-page program!
  ### I like React
  I *wanted* to use it **obviously\!**
  ___
  [links](https://gabester.tech)
  > Does this spark joy?
  tick | tac
  ---- | ----
  toe | spell?

  1 I
  - am
  - a
  - list
  ![React Logo w/ Text](https://goo.gl/Umyytc)
`
  {/*'# Why React? \r ## I certainly didn\'t need if for my 2-component 1-page program! \r ### I like React \r I *wanted* to use it **obviously\!** \r ___ \r `background-color: white` \r [links](https://gabester.tech) \r > Does this spark joy? \r  tick | tac \r ---- | ---- \r toe | spell?' */}

class Home extends Component{
    state = {
        input: content
    };

    componentDidMount(){
        const script = document.createElement("script");
        script.src = 'https://cdn.freecodecamp.org/testable-projects-fcc/v1/bundle.js';
        script.async = true;
        document.body.appendChild(script);
    }

    changeListener(event) {
        this.setState({
            input: event.target.value
        })
    }

    getMarkdown = () =>{
        const markdown = marked(this.state.input, {sanitize: true});
        return {__html: markdown};
    }

    render() {

        return (
            <div >
                <h1>Hallo with Next.js and Marked.js</h1>
                    <Input change={(event) => this.changeListener(event) } />
                    <Output setMarkdown={this.getMarkdown()} />
                    <style jsx>{`
                        div {
                            width: 800px;
                            margin: 75px auto;
                            border; 2px solid black;
                            text-align: center;
                        }
                    `}</style>
            </div>
        );
    }
}

export default Home;